@import url(https://fonts.googleapis.com/css2?family=Teko:wght@500&display=swap);

html, body{
  margin:0;
  padding:0;
}

.App{
  height:100vh;
  width:100vw;
  font-family:'Teko';
  color:white;
  font-size:50px;
  display:-webkit-flex;
  display:flex;
  -webkit-align-content:flex-start;
          align-content:flex-start;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;

  background-image:url('https://media.giphy.com/media/3oz8xBvY5MyxIdyuxa/source.gif');
  background-position: center;
  background-size:cover;

}

.title-row{
  width:100%;
}
.title{
  display:inline-block;
  padding:0.5em;

}

.title:hover{
  color:yellow;
}

.start-game{
  width:100%;
  height:60%;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  -webkit-align-content: center;
          align-content: center;

}
.enter-name-label{
  width:100%;
  text-align:center;
}

.name-form{
  text-align:center;
}

.name-input{
  font-family:'Teko';
  height:1.5em;
  font-size:50px;
  outline:none;
  text-align:center;
  opacity:0.7;
}

.start-game-btn-row{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  width:100%;
}
.start-game-btn{
  display:inline-block;
  text-align:center;
}

.start-game-btn:hover{
  color:yellow;
}

.fadeOut{
  -webkit-animation: fadeOut 4s linear forwards;
          animation: fadeOut 4s linear forwards;
}

@-webkit-keyframes fadeOut{
  0% {opacity:1;}
  50%{opacity:0.2;}
  100%{opacity:0;}
}

@keyframes fadeOut{
  0% {opacity:1;}
  50%{opacity:0.2;}
  100%{opacity:0;}
}

.arrange-board{
  width:100%;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  background-color: rgba(218, 223, 225, 0.75);
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding:1vh;
}

.game-page{
  width:100%;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  background-color: rgba(218, 223, 225, 0.75);
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding:1vh;
}

.place-carriers-label{
  width:100%;
  font-size:35px;
}

.change-orientation{
  border: solid white 2px;
  padding-left: 0.5em;
  padding-right: 0.5em;
  font-size:35px;
  margin-bottom:0.4em;
}

.change-orientation:hover{
  color:yellow;
}

.arrange-gameboard-container{
  width:100%;
}

.gameboards-container{
  width:100%;
  display:-webkit-flex;
  display:flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.gameboard{
  display:grid;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  grid-template-columns:repeat(10,6.4vh);
  grid-template-rows:repeat(10,6.4vh);
  padding-bottom:1vh;
}

.main-gameboard{
  display:grid;
  grid-template-columns:repeat(10,6.4vh);
  grid-template-rows:repeat(10,6.4vh);
  margin-left:5vh;
  margin-right:5vh;
  padding-bottom:1vh;
}

.player-one-label{
  width:100%;
}

.grid-element{
  min-height:6vh;
  min-width:6vh;
  margin:0.2vh;
  border: solid black 1px;
}

.highlight{
  background-color:white;
}

